	max-width: 35rem;
	padding-left: 0rem;
	// margin-left: auto;
	// margin-right: auto;
	display: block;
	font-size: 18px;
}

#cv .normal-web-width p {
	font-size: 18px;
	margin-left: 0rem;
}

