// Layout
//
// Styles for managing the structural hierarchy of the site.

.container {
  max-width: 55rem;
  /*max-width: 38rem;*/
  padding-left:  1rem;
  padding-right: 1rem;
  margin-left:  auto;
  margin-right: auto;
}

footer {
	background-color: #1E1E24;
	color: #e6e6e6;
	// border-top: 1px #ECECEC solid;
	padding-bottom: 2rem;
	padding-top: 1rem;
	margin-top: 2em;

	@media (max-width: 41em) {
    padding-left: 1em;
    padding-right: 1em,;
  }

}

.footer {
	max-width: 45em;
	margin-left: auto;
	margin-right: auto;
	margin-top: 1em;
	display: flex;
	padding: 20px;
}

.footer-half-l, .footer-half-r {
	flex: 1;
}

.footer-half-l {
	// display: flex;
	// flex-direction: column;
	// justify-content: center;
}

@media screen and (max-width: $mobile) {
	.footer {
		display: block;
	}

	.footer-half-l {
		padding-bottom: 20px;
	}
}

div.social {
	display: block;
	height: 2.25em;
}

img.social-icon {
	float: left;
    max-width: 1.5em;
    margin-right: 1em;
}


//Center the element in .container below

h1, h2, h3, h4, h5, h6, p, blockquote, ul, ol, dl, table, .message, .highlight, .gist-file, .pagination {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  max-width: 40rem;
  margin-left: auto;
  margin-right: auto;
}

ul, ol {
  padding-left: 2.5rem;
  padding-right: 2.5rem;
  max-width: 40rem;
  margin-left: auto;
  margin-right: auto;
}

.comments{
  padding-left: 1.5rem;
  padding-right: 1.5rem;
	max-width: 40em;
	margin-right: auto;
	margin-left: auto;
}

hr {
	max-width: 40em;
	margin-left: auto;
	margin-right: auto;
}
