// Posts and pages
//
// Each post is wrapped in `.post` and is used on default and post layouts. Each
// page is wrapped in `.page` and is only used on the page layout.

.page,
.post {
  margin-bottom: 0em;
  padding-left: 0rem;
}

// Blog post or page title
.page-title {
  padding-left: 1.5rem;
  font-size: 2.2em; 
}


.post-title {
  color: #357edd;
  font-size: 1.7em; 
}


.page-title,
.post-title {
  margin-top: 1rem;
}

// Meta data line below post title
.post-date {
  display: block;
  margin-top: -.5rem;
  margin-bottom: 1rem;
  color: #9a9a9a;
}


// Related posts
.related {
  padding-top: 2rem;
  padding-bottom: 2rem;
  border-top: 1px solid #eee;
}

.related-posts {
  padding-left: 0;
  list-style: none;

  h3 {
    margin-top: 0;
  }

  li {
    small {
      font-size: 75%;
      color: #999;
    }

    a:hover {
      color: #2196F3;
      text-decoration: none;

      small {
        color: inherit;
      }
    }
  }
}


